          <input type="text" v-model="blog.title"/><br/>
          <label>博客内容</label>
          <textarea v-model="blog.content"></textarea>
          <div id="checkboxes" v-model="blog.categorises">
              <label>vue.js</label>
              <input type="checkbox" value="vue.js" v-model="blog.categorises"/>
              <label>node.js</label>
              <input type="checkbox" value="node.js" v-model="blog.categorises"/>
              <label>React.js</label>
              <input type="checkbox" value="React.js" v-model="blog.categorises"/>
              <label>Angular.js</label>
              <input type="checkbox" value="Angular.js" v-model="blog.categorises"/><br/>
          </div>
          <label>作者</label>
          <select v-model="blog.author">
              <option v-for="author in authors">
                  {{author}}
              </option>
          </select>
          <button @click.prevent="post">添加博客</button>
      </form>
      <p v-if="submmited">{{ message }}</p>
      <hr>
      <div id="preview">
