<script>
// @ is an alias to /src
export default {
    name:'add-blog',
    data(){
        return {
            blog:{
                title:'',
                content:'',
                author:'',
                categorises:[]
            },
            authors:['heZhao1','heZhao2','heZhao3',],
            submmited:false,
            message:''
