<template>
  <div id="single-blog">
     <h3>{{blog.title}}</h3>
     <article class="blog">{{blog.content}}</article>
      <p>作者：</p>
      <p>{{ blog.author }}</p>
      <p>分类：</p>
      <ul>
          <li v-for="categorise in blog.categorises">
              {{categorise}}
          </li>
      </ul>

  </div>
</template>

<script>
// @ is an alias to /src
export default {
    name:'single-blog',
    data(){
        return {
            id:this.$route.params.id,
            blog:{},
            categorises:[]
        }
    },
    created(){
        this.$http.get('https://wd4161855934rfbyyx.wilddogio.com/posts/' + this.id + ".json")
            .then(data =>{
                this.blog = data.data

            })
    }
}
</script>
