<template>
  <div class="show-blogs">
      <h3>博客总览</h3>
      <!--搜索框-->
      <input type="text" v-model="search" placeholder="搜索"/>
      <div class="blog" v-for="blog in filterBlogs">
          <router-link v-bind:to=" '/blog/' + blog.id ">
              <p class="title" v-rainbow>{{blog.title}}</p>
          </router-link>
          <p class="article">{{blog.content | snippet}}</p>
      </div>
  </div>
</template>

<script>

export default {
    name: 'show-blogs',
    data () {
        return {
            blogs:[],
            search:'',
        }
    },
    created:function () {
        // this.$http.get('https://jsonplaceholder.typicode.com/posts')
        this.$http.get('https://wd4161855934rfbyyx.wilddogio.com/posts.json')
            .then(function(data){
                return (data.json())
            })
            .then(function (data) {
                const blogArray = [];
                for(let key in data) {
                    data[key].id = key;
                    blogArray.push(data[key]);
                }
                this.blogs = blogArray;
            })
    },
    computed:{
        // 实现搜索功能...
        filterBlogs:function () {
            return this.blogs.filter( (blog) => {
                return blog.title.match(this.search)
            })
        }
    }
}
</script>
