import Vue from 'vue'
import Router from 'vue-router'
import ShowBlogs from './views/ShowBlogs.vue'
import AddBlog from './views/AddBlog.vue'
import SingleBlog from './views/SingleBlog.vue'

Vue.use(Router)

export default new Router({
    mode:'history',
    routes: [
        {
            path: '/',
            name: 'ShowBlogs',
            component: ShowBlogs
        },
        {
            path: '/index',
            name: 'ShowBlogs',
            component: ShowBlogs
        },
        {
            path: '/add',
            name: 'AddBlog',
            component: AddBlog
        },
        {
            path: '/blog/:id',
            name: 'SingleBlog',
            component: SingleBlog
        },
    ]
})
